
.cp-form_item {
  margin-bottom: 5px;
}
.cp-form_item label {
  display: inline-block;
  width: 200px;
}
.cp-form_item input,.cp-form_item select,.cp-form_item textarea {
  display: inline-block;
  width: 400px;
  line-height: 24px;
  box-sizing: border-box;
  font-size: 14px;
  padding: 2px;
}
.cp-form_tip {
  font-size: 14px;
}



