
input,select,textarea {
  outline: none;
}
a {
  cursor: pointer;
}
#app {
  margin: 10px auto;
  width: 960px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 18px;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
  padding: 10px;
  width: 100%;
}
.cp-desc {
  font-weight: bold;
  font-style: italic;
  color: #555;
}
.cp-btn {
  font-size: 16px;
  font-style: normal;
  padding: 2px 10px;
  border-radius: 3px;
  color: #fff;
  background-color: #42b983;
  float: right;
}
.cp-btn.warn {
  background-color: #f60;
}
.cp-input_pluskey {
  width: 200px !important;
}



